import React, { useState } from 'react';
import ProfileImageWithDefault from './ProfileImageWithDefault';
import { Link } from 'react-router-dom';
import { format } from 'timeago.js'
import { useTranslation } from 'react-i18next';
import { useSelector } from 'react-redux';
import { deleteHoax } from '../api/apiCalls';
import Modal from './Modal';
import { useApiProgress } from '../shared/ApiProgress';

const HoaxView = (props) => {

    const loggedInUser = useSelector(store => store.username)

    const { hoax, onDeleteHoax } = props;
    const { userVM, content, timestamp, fileAttachmentVM, id } = hoax;
    const { username, displayName, image } = userVM;
    const { i18n, t } = useTranslation();
    const formatted = format(timestamp, i18n.language);
    const [modalVisible, setModalVisible] = useState(false)

    const ownedByLoggedInUser = loggedInUser === username;

    const pendingApiCall = useApiProgress('delete', `/api/1.0/hoaxes/${id}`, true)

    const onClickDelete = async () => {
        await deleteHoax(id);
        onDeleteHoax(id);
    }

    const onClickCancel = () => {
        setModalVisible(false);
    }

    return (
        <>
            <div className="card p-1">
                <div className="d-flex">
                    <ProfileImageWithDefault
                        image={image}
                        width="32"
                        height="32"
                        className="rounded-circle m-1"
                    />
                    <div className="flex-fill m-auto pl-2">
                        <Link to={`/user/${username}`} className="text-dark">
                            <h6 className="d-inline">
                                {displayName} @ {username}
                            </h6>
                            <span> - </span>
                            <span>{formatted}</span>
                        </Link>
                    </div>
                    {ownedByLoggedInUser && (
                        <button className="btn btn-delete-link btn-sm"
                            onClick={() => setModalVisible(true)}>
                            <i className="material-icons">delete_outline</i>
                        </button>
                    )}
                </div>
                <div className="pl-5">
                    {content}
                </div>
                {fileAttachmentVM && (
                    <div className="pl-5">
                        {fileAttachmentVM.fileType.startsWith('image') && (
                            <img
                                className="img-fluid"
                                src={'images/attachments/' + fileAttachmentVM.name}
                                alt={content}
                            />
                        )}
                        {!fileAttachmentVM.fileType.startsWith('image') && (
                            <strong>Hoax has unknown attachment</strong>
                        )}
                    </div>
                )}
            </div>
            <Modal
                visible={modalVisible}
                title={t('Delete Hoax')}
                onClickCancel={onClickCancel}
                onClickOk={onClickDelete}
                pendingApiCall={pendingApiCall}
                message={
                    <div>
                        <div><strong>{t('Are you sure to delete hoax?')}</strong></div>
                        <span>{content}</span>
                    </div>
                }
                okButton={t('Delete Hoax')}
            />
        </>
    );
};

export default HoaxView;